@media only screen and (max-width: 800px) {
    .header {
        position: relative;
        display: flex;
        justify-content: space-between;
        padding: 0;
    }
    .scroll-image {
        top: 539.111px;
        position: absolute;
        top: 0;
        width: 100%;
        height: 200px;
        filter: brightness(40%);
        z-index: -1;
    }
    .header-section {
        display: flex;
        flex-direction: column;
        flex: 1;
    }
    .left {
        margin-top: 0px;
        margin-left: 0px;
    }
    
    .right {
        line-height: 22px;
        margin-top: auto;
        margin-bottom: auto;
        margin-right: 0px;
    }
    
    .left .profile-pic {
        border-radius: 30%;
        margin-bottom: 0;
        margin-top: 10px;
        width: 130px;
        height: 150px;
        object-fit: cover;
    }
    #myName {
        margin-left: 5px;
        margin-top: 0;
        color: #ffffff;
        font-size: 20px;
    }
    #contact {
        text-align: center;
        margin-top: auto;
        margin-bottom: 10px;
        color: #ffffff;
        font-size: 20px;
    }
    
    .right a {
        margin-right: 10px;
        color: #1E90FF; 
        font-size: 14px;
        text-decoration: underline;
        margin: 5px 0;
    }
    
    .right a:hover {
        color: #ff1ef8;
    }
    
    .right p {
        margin-top: 5px;
        margin-bottom: 0;
        margin-right: 2px;
        color: #ffffff;
        font-size: 12px;
        text-align: center;
    }
    
    #me {
        border: 1.5px solid black;
        width: 90%;
        padding: 10px;
        text-align: center;
        color: #000000;
        font-size: large;
        font-weight: 600;
        position: relative;
        margin-top: 20px;
        margin-bottom: 0;
        margin-left: auto;
        margin-right: auto;
    }

    #MdEmail {
        font-size: 18px;
    }
    #BsFillTelephoneForwardFill {
        font-size: 18px;
    }
    #FaHome {
        font-size: 18px;
    }
    #AiOutlineGithub {
        font-size: 21px;
    }
    #FaLinkedin {
        font-size: 19px;
    }
}

@media only screen and (min-width: 800px) {
    .header {
        position: relative;
        display: flex;
        justify-content: space-between;
        padding: 0;
    }

    .scroll-image {
        top: 539.111px;
        position: absolute;
        top: 0;
        width: 100%;
        height: 600px;
        filter: brightness(40%);
        z-index: -1;
    }

    .header-section {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        flex: 1;
    }

    .left {
        margin-top: 150px;
        margin-left: 100px;
    }

    .right {
        line-height: 30px;
        margin-top: 150px;
        margin-right: 100px;
    }

    .left .profile-pic {
        margin-bottom: 0;
        border-radius: 50%;
        width: 200px;
        height: 200px;
        object-fit: cover;
    }

    .header h1 {
        margin: 0;
        color: #ffffff;
        font-size: 32px;
    }

    .right a {
        margin-right: 10px;
        color: #1E90FF;
        font-size: 22px;
        font-weight: 600;
        text-decoration: underline;
        margin: 5px 0;
    }

    .right a:hover {
        color: #ff1ef8;
    }

    .right p {
        margin: 0;
        color: #ffffff;
        font-size: 18px;
        text-align: center;
        padding: 5px;
    }

    .light-blue-icon {
        color: #135fca;
    }

    #me {
        border: 1px rgb(221, 214, 214);
        width: 40%;
        padding: 10px;
        color: white;
        font-size: large;
        position: relative;
        margin-top: 50px;
        margin-bottom: 50px;
        margin-left: 20%;
        margin-right: auto;
    }

    #contact {
        text-align: center;
        margin-top: auto;
        margin-bottom: 20px;
    }

    #MdEmail {
        font-size: 28px;
    }

    #BsFillTelephoneForwardFill {
        font-size: 25px;
    }

    #FaHome {
        font-size: 28px;
    }

    #AiOutlineGithub {
        font-size: 35px;
    }

    #FaLinkedin {
        font-size: 30px;
    }
}